.JSAnimation {
	width: 125px;
	color: red; /* 
	animation-name: move;
	animation-duration: 4s;
	animation-iteration-count: infinite; */
	position: absolute;
	left: auto;
	top: auto;
	transform-origin: center;
	transform: translate(80%, 150%);
}
.js-animation-title {
	font-size: 4em;
	display: flex;
	text-align: center;
	margin: auto;
}
.js-animation-line {
	font-size: 4em;
	margin-top: -80px;
	overflow: hidden;
	display: flex;
	font-weight: 600;
	/* 
	animation-name: line;
	animation-duration: 4s;
	animation-iteration-count: infinite; */
	width: 60px;
}

@-webkit-keyframes line {
	0% {
		width: 0px;
	}
	10% {
		width: 0px;
	}
	30% {
		width: 60px;
	}
	100% {
		width: 60px;
	}
}

@keyframes line {
	0% {
		width: 0px;
	}
	10% {
		width: 0px;
	}
	30% {
		width: 60px;
	}
	100% {
		width: 60px;
	}
}

@-webkit-keyframes move {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 1;
	}
	30% {
		transform: rotate(0deg) translate(80%, 150%);
	}
	55% {
		transform: rotate(-25deg) translate(50%, 200%);
	}
	100% {
		transform: rotate(-25deg) translate(250%, 350%);
	}
}

@keyframes move {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 1;
	}
	30% {
		transform: rotate(0deg) translate(80%, 150%);
	}
	55% {
		transform: rotate(-25deg) translate(50%, 200%);
	}
	100% {
		transform: rotate(-25deg) translate(250%, 350%);
	}
}

